.header_container {
    max-width: 1920;
    width: 100%;
    height: 100px;
    background-color: rgba(184, 135, 11, 0.825);
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0 auto;
}

.header {

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

nav {
    width: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 20px;
}

.active div {
    background-color: #878ca1;
    border-radius: 70px;
}

.header_p {
    color: gold;
    font-weight: bold;
    font-size: 20px;
    margin-left: 10px;
}